.container{
    margin-top: 1rem;
}
.contato{
    width: 90%;
    margin: auto;
    background-color: #F5F3F3;
    padding: 2rem;
    border-radius: 1rem;
    border-style: solid;
    border-width: 0.1px;
    border-color: rgba(0, 0, 0, 0.166);
}

.container button{
    background-color: #02351B;
    border-style: none;
    color: white;
    cursor: pointer;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;

}